.canvas {
    width: 50%;
    height: 50%;
    border-radius: 4px;
    margin-bottom: 1%;
  }
  
  /* Adjust width for screen sizes below 'xl' */
  @media (max-width: 1199px) {
    .canvas {
      width: 70%;
    }
  }
  
  /* Adjust width for screen sizes below 'lg' */
  @media (max-width: 991px) {
    .canvas {
      width: 80%;
    }
  }
  
  /* Adjust width for screen sizes below 'md' */
  @media (max-width: 767px) {
    .canvas {
      width: 90%;
    }
  }
  
  /* Adjust width for screen sizes below 'sm' */
  @media (max-width: 575px) {
    .canvas {
      width: 96%;
    }
  }