.header{
   color: white;
   width: 50%;
}
  
  /* Adjust width for screen sizes below 'xl' */
  @media (max-width: 1199px) {
    .header {
      width: 70%;
    }
  }
  
  /* Adjust width for screen sizes below 'lg' */
  @media (max-width: 991px) {
    .header {
      width: 80%;
    }
  }
  
  /* Adjust width for screen sizes below 'md' */
  @media (max-width: 767px) {
    .header {
      width: 90%;
    }
  }
  
  /* Adjust width for screen sizes below 'sm' */
  @media (max-width: 575px) {
    .header {
      width: 96%;
    }
  }