.formContainer {
    display: flex;
    justify-content: space-between; /* This will space out children */
    align-items: center; /* Centers content vertically within the container */
    width: 50%; /* Adjust based on your layout */
    padding-top: 10px; /* Adds some padding inside the container, if desired */
    box-sizing: border-box; /* Ensures padding is included in the container's dimensions */
  }

  .input {
    width: 30%; /* Adjusted to complement the button width */
    padding: 3px; /* Matches the button padding */
    border: none; /* Matches the button border styling */
    border-radius: 4px; /* Rounded corners, like the button */
    font-size: 16px; /* Ensures text size consistency with the button */
    margin: 5px 0; /* Adds space between input fields and the button */
    box-sizing: border-box; /* Ensures padding doesn't add to the width */
    transition: border-color 0.3s ease; /* Smooth transition for border color */
  }
  
  .input:focus {
    border-color: #007bff; /* Highlight color on focus */
    outline: none; /* Removes the default focus outline */
  }

  .button{
    width: 15%;
    padding: 3px;
    border: none; /* Removes default border */
    border-radius: 4px; /* Rounded corners */
    background-color: #007bff; /* Primary button color */
    color: white; /* Text color */
    font-size: 16px;
    cursor: pointer; /* Changes cursor to pointer on hover */
    transition: background-color 0.3s ease; /* Smooth transition for background color */
  }

  .button:hover {
    background-color: #0056b3; /* Darker shade on hover */
  }
  
  .button:active {
    background-color: #004094; /* Even darker shade when clicked */
  }

  .button:disabled {
    /* styles for disabled button */
    background-color: #cccccc;
    cursor: default;
  }

  .text{
      color: white;
      margin: 0;
  }
  
  
  .form {
    display: flex;
    width: 100%; /* Ensures form takes full width of its container */
    justify-content: space-between; /* Spaces out the children */
    align-items: center; /* Align items vertically in the center */
  }
  /* Adjust width for screen sizes */
  @media (max-width: 1199px) {
    .formContainer {
      width: 70%;
    }
  }
  
  @media (max-width: 991px) {
    .formContainer {
      width: 80%;
    }
  }
  
  @media (max-width: 767px) {
    .formContainer {
      width: 90%;
    }
  }
  
  @media (max-width: 575px) {
    .formContainer {
      width: 96%;
    }
  }
  