@import url('https://fonts.googleapis.com/css?family=Poppins&display=swap');

.App {
  font-family: 'Poppins', sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: aliceblue;
  background-image: url('./Endless-Constellation.svg');
  
}

/* Default width for 'xl' and above */
.App-content {
  width: 60%;
}

/* Adjust width for screen sizes below 'xl' */
@media (max-width: 1199px) {
  .App-content {
    width: 70%;
  }
}

/* Adjust width for screen sizes below 'lg' */
@media (max-width: 991px) {
  .App-content {
    width: 80%;
  }
}

/* Adjust width for screen sizes below 'md' */
@media (max-width: 767px) {
  .App-content {
    width: 90%;
  }
}

/* Adjust width for screen sizes below 'sm' */
@media (max-width: 575px) {
  .App-content {
    width: 96%;
  }
}